.risk-menu {
	position: absolute;
	width: 340px;
	height: 240px;
	left: 50%;
	margin-left: -170px;
	bottom: 20px;
	background: #2B3250;
	box-shadow: 0px 16px 40px rgba(11, 16, 40, 0.94);
	border-radius: 8px;
	z-index: 100;
	color: #fff;
	font-size: 14px;
	cursor: move;
}

.risk-menu.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.risk-menu.close {
    display: none;
} 

.risk-menu:after {
	content: '';
	position: absolute;
	width: 6px;
	height: 200px;
	left: -3px;
	top: 20px;
	background: #F2696D;
	border-radius: 8px;
	z-index: 2;
}

.risk-menu__header {
	display: flex;
	align-items: center;
	font-weight: 500;
}

.risk-menu__image img {
	width: 80px;
	height: 80px;
}

.risk-menu__items {
	border-top: 1px solid rgba(88, 114, 218, 0.2);
}

.risk-menu__item {
	padding: 12px 24px;
	cursor: pointer;
	border-bottom: 1px solid rgba(88, 114, 218, 0.2);
}

.risk-menu__item:hover {
	background: rgba(88, 114, 218, 0.2);
}

.risk-menu__close {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}

.risk {
	position: absolute;

	left: 50%;
	margin-left: -170px;
	bottom: 20px;
	background: #2B3250;
	box-shadow: 0px 16px 40px rgba(11, 16, 40, 0.94);
	border-radius: 8px;
	z-index: 100;
	color: #fff;
	font-size: 14px;
	cursor: move;
}

.risk.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.risk.close{
	display: none;
}

.risk__info {
	padding-right: 50px;
}

.risk__header {
	display: flex;
	align-items: center;
	font-weight: 500;
	min-height: 90px;
}

.risk__image {
	padding: 0 15px;
    line-height: 0;
}

.risk__image img {
    width: 50px;
    height: 50px;
}

.risk__close {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}

.risk__inn {
	background: rgba(88, 114, 218, 0.2);
	padding: 11px 24px;
}

.risk__inn>span {
	color: #859DE0;
	margin-right: 5px;
}

.risk__drag{
	height: 100%;
	display: flex;
	flex-direction: column;
}

.risk__items{
	height: 100%;
}

.risk__item {
	padding: 0 24px;
}

.risk__item-title {
	color: #859DE0;
	font-weight: 500;
	margin-top: 14px;
	margin-bottom: 6px;
}

.risk__item-item {
	font-weight: 500;
	margin-bottom: 4px;
	letter-spacing: 1px;
}

.risk__button {
    float: left;
    margin: 12px 24px 24px;
}

.risk__resort-rntp {
    cursor: pointer;
    background: #D2E8FF;
    padding: 10px;
    font-size: 12px;
    border-radius: 4px;
	margin-bottom: 8px;
	color: #064FA3;
	transition: all 150ms;
}

.risk__resort-rntp:hover {
	background: #064FA3;
	color: #fff;
}

.rntp_item {
    margin-bottom: 5px;
}

.rntp_title {
    color: #859DE0;
}

.react-resizable {
	position: relative;
  }
  .react-resizable-handle {
	position: absolute;
	width: 20px;
	height: 20px;
	background-repeat: no-repeat;
	background-origin: content-box;
	box-sizing: border-box;
	background-image: url('data:image/svg+xml;base64,');
	background-position: bottom right;
	padding: 0 3px 3px 0;
  }
  .react-resizable-handle-sw {
	bottom: 0;
	left: 0;
	cursor: sw-resize;
	transform: rotate(90deg);
  }
  .react-resizable-handle-se {
	bottom: 0;
	right: 0;
	cursor: se-resize;
  }
  .react-resizable-handle-nw {
	top: 0;
	left: 0;
	cursor: nw-resize;
	transform: rotate(180deg);
  }
  .react-resizable-handle-ne {
	top: 0;
	right: 0;
	cursor: ne-resize;
	transform: rotate(270deg);
  }
  .react-resizable-handle-w,
  .react-resizable-handle-e {
	top: 50%;
	margin-top: -10px;
	cursor: ew-resize;
  }
  .react-resizable-handle-w {
	left: 0;
	transform: rotate(135deg);
  }
  .react-resizable-handle-e {
	right: 0;
	transform: rotate(315deg);
  }
  .react-resizable-handle-n,
  .react-resizable-handle-s {
	left: 50%;
	margin-left: -10px;
	cursor: ns-resize;
  }
  .react-resizable-handle-n {
	top: 5px;
	transform: rotate(225deg);
  }
  .react-resizable-handle-s {
	bottom: 0;
	transform: rotate(45deg);
  }