.right-menu {
	background: #2b3250;
	position: absolute;
	color: #fff;
	left: 8px;
    top: 68px;
	padding: 8px;
	width: 56px;
	border-radius: 8px;
	z-index: 100;
	overflow: hidden;
	box-sizing: border-box;
	height: calc(100% - 68px);
	max-height: 568px;
}

.right-menu.light {
	background: #fff;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
	color: #2b3250;
}

.right-menu__item {
	height: 40px;
    text-align: center;
    line-height: 40px;
    font-weight: 500;
    cursor: pointer;
    border-radius: 6px;
    font-size: 18px;
    margin-bottom: 6px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
}

.right-menu__item:nth-child(9) .right-menu__image, .right-menu__item:nth-child(10) .right-menu__image {
	padding-left: 4px;
}

.right-menu__image {
    display: flex;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
}

.right-menu__item img {
	opacity: 0.4;
	max-width: 30px;
}

.right-menu__item.active img {
	opacity: 1;
}

.right-menu__item:hover {
	background: rgba(255, 255, 255, 0.1);
}

.light .right-menu__item:hover {
	background: rgba(41, 44, 67, 0.1);
}

.right-menu__more {
	position: absolute;
	left: 8px;
	bottom: 3px;
	background: #2b3250;
}

.right-menu__more:hover {
	background: #424a6b;
}

.light .right-menu__more {
	background: #fff;
}

.light .right-menu__more:hover {
	background: #e9e9ec;
}

.right-menu__title {
	display: none;
	font-size: 14px;
    font-weight: 400;
    margin-left: 10px;
    margin-right: 16px;
}

.right-menu.open {
    height: calc(100% - 58px);
    max-height: 100%;
    padding: 18px 16px;
    left: 0;
    top: 58px;
    border-radius: 0;
    width: 432px;
}

.right-menu.open .right-menu__title {
    display: inline-block;
}

.right-menu.collapsed .right-menu__more {
	display: none;
}

.right-menu.open .__react_component_tooltip {
    display: none !important;
}